import React, { useState, Component } from "react";
import "./App.css";

const api = {
  key: "7c60e31c12f6b0f3c92c88fbcd47ce86",
  base: "https://api.openweathermap.org/data/2.5/",
};

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      query: "",
      weather: "",
    };
  }

  search = (evt) => {
    if (evt.key === "Enter") {
      fetch(
        `${api.base}weather?q=${this.state.query}&units=metric&APPID=${api.key}`
      )
        .then((response) => response.json())
        .then((result) => {
          this.setState({ query: "", weather: result }, () => {
            console.log(this.state.weather);
          });
        });
    }
  };
  dateBuilder = (d) => {
    let months = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];
    let days = [
      "Sunday",
      "Monday",
      "Tuesday",
      "Wednesday",
      "Thursday",
      "Friday",
      "Saturday",
    ];
    let day = days[d.getDay()];
    let date = d.getDate();
    let month = months[d.getMonth()];
    let years = d.getFullYear();
    return `${day} ${date} ${month} ${years}`;
  };
  handleOnChange = (e, type) => {
    e.preventDefault();
    console.log(type, "type");
    this.setState({ query: e.target.value });
  };
  render() {
    return (
      <div
        className={
          typeof this.state.weather.main !== "undefined"
            ? this.state.weather.main.temp > 25
              ? "Appwarm"
              : "App"
            : "App"
        }
      >
        <main>
          <div className="heading">
            <p>YOU CAN FIND WEATHER FROM HERE</p>
          </div>
          <div className="search-box">
            <input
              type="text"
              className="search-bar"
              placeholder="please enter city name here..."
              onChange={(e) => this.handleOnChange(e, "type")}
              value={this.state.query}
              onKeyPress={this.search}
            ></input>
          </div>
          {typeof this.state.weather.main !== "undefined" ? (
            <div>
              <div className="location-box">
                <div className="location">
                  {this.state.weather.name}, {this.state.weather.sys.country}
                </div>
                <div className="date">{this.dateBuilder(new Date())}</div>
              </div>
              <div className="weather-box">
                <div className="temp">
                  {Math.round(this.state.weather.main.temp)}Â°C
                </div>
                <div className="weather">
                  {this.state.weather.weather[0].main}
                </div>
              </div>
            </div>
          ) : (
            " "
          )}
        </main>
      </div>
    );
  }
}

export default App;
